@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  font-family: 'Open Sans', sans-serif;
  background: url(https://www.brainpickings.org/wp-content/themes/brainpickings/images/bckgd_body.png);
}

.main-container {
  position: relative;
  min-height: 100vh;
}

.button-container {
  height: 70px;
  margin-top: 8%;
  margin-left: 80%;
  margin-right: 0%;
  display: inline-block;
}

header {
    width: 100%;
    height: 100px;
    background: #000205;
    position: fixed;
    z-index: 2;
    box-shadow: 0 4px 4px rgba(4, 50, 119, 0.1);
}

.small {
  height: 70px;
}

.small .title {
    font-size: 3em;
    margin-top: 5px;
}

nav {
    width: 80%;
    margin: 0 auto;
}

.title {
    font-family: 'Lobster', cursive;
    font-size: 4em;
    font-weight: 400;
    color: white;
    float: left;
    padding: 5px;
    margin-top: 10px;
}


header, .title{
    transition: all 0.3s; 
}
.calendar-card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  padding: 10px;
}

.calendar-card {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 70%;
  max-width: 700px;
  border: 2px solid #d5d5d5;
  border-radius: 5px;
  cursor: pointer;
}

.card-header {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #000205;
  background: #f3f3f3;
  padding: 18px;
  padding-bottom: 5px;
  position: relative;
}

.calendar-box {
  width: 25%;
  border: 1px solid #000205;
  margin: 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.calendar-box>.month {
  background: #000205;
  color: white;
  font-size: 3rem;
  width: 100%;
  text-align: center;
}

.calendar-box>.day {
  font-size: 4rem;
  color: #000205;
}

.card-text-aside {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 2rem;
  color: #000205;
  margin-left: 10px;
}

.card-text-aside>.time {
  font-size: 1.8rem;
}

.card-text-aside>.notes {
  font-size: 1.5rem;
  font-style: italic;
}

.calendar-right {
  display: flex;
  position: absolute;
  right: 5px;
  top: 5px;
  color: #000205;
}

.calendar-right>.far {
  font-size: 3rem;
}

/*
   * shake
   */

.shake {
  -webkit-animation-duration: 0.8s;
  -webkit-animation-name: shake;
  animation-duration: 0.8s;
  animation-name: shake;
}

@-webkit-keyframes shake {
  from, to {
    transform: translate3d(0, 0, 0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translate3d(-10px, 0, 0);
  }
  20%, 40%, 60%, 80% {
    transform: translate3d(10px, 0, 0);
  }
}

@keyframes shake {
  from, to {
    transform: translate3d(0, 0, 0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translate3d(-10px, 0, 0);
  }
  20%, 40%, 60%, 80% {
    transform: translate3d(10px, 0, 0);
  }
}
#social-footer {
    width: 100%;
    text-align: center;
    color: white;
    background-color: black;
}

#social-footer ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

#social-footer ul li {
    display: inline;
    padding: 0.4em;
}

#social-footer a {
    color: white;
}

.blacktext {
    color: black;
}

footer {
    width: 100vw;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 1% 5%;
    text-align: center;
    background-color: #000;
    opacity: 1;
    z-index: 100;
}

a.fa {
    text-decoration: none
}

a:link {
    text-decoration: none !important;
    outline: 0;
}

.copyright {
    padding-bottom: 0px;
    padding-top: 5px;
    color: #555;
}
